import { Component, OnInit, EventEmitter, Input, Output } from '@angular/core';
import { NgPaginationService } from './ng-pagination.service';

@Component({
  selector: 'ngPagination',
  template: `
  <div class="container">
    <ul *ngIf="pager.pages && pager.pages.length" class="pagination">
        <li [ngClass]="{disabled:pager.currentPage === 1}">
            <a (click)="setPage(1)">First</a>
        </li>
        <li [ngClass]="{disabled:pager.currentPage === 1}">
            <a (click)="setPage(pager.currentPage - 1)">&laquo;</a>
        </li>
        <li *ngFor="let page of pager.pages" [ngClass]="{active:pager.currentPage === page}">
            <a (click)="setPage(page)">{{page}}</a>
        </li>
        <li [ngClass]="{disabled:pager.currentPage === pager.totalPages}">
            <a (click)="setPage(pager.currentPage + 1)">&raquo;</a>
        </li>
        <li [ngClass]="{disabled:pager.currentPage === pager.totalPages}">
            <a (click)="setPage(pager.totalPages)">Last</a>
        </li>
    </ul>
    <span *ngIf='showTotal'>{{pager.startIndex +1}} - {{pager.endIndex +1}} out of {{allItems.length}}</span>
</div>
`,
  styles: [
    `
  .container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

ul {
    padding-inline-start: 0;
}

.pagination li {
    cursor: pointer;
    user-select: none;
    display: inline-block;
    border-radius: 2px;
    text-align: center;
    vertical-align: top;
    height: 30px;
}

.pagination li a {
    color: #444;
    display: inline-block;
    font-size: 1.2rem;
    padding: 0 10px;
    line-height: 30px;
}

.pagination li.active a {
    color: #fff;
}

.pagination li.active {
    background-color: #ee6e73;
}

.pagination li.disabled a {
    cursor: default;
    color: #999;
}

.pagination li i {
    font-size: 2rem;
}

.pagination li.pages ul li {
    display: inline-block;
    float: none;
}
`]
})
export class NgPaginationComponent implements OnInit {
  @Input() allItems: any[];
  @Input() pageSize: number;
  @Input() maxPages: number;
  @Input() showTotal: boolean;
  @Output() pagedItems = new EventEmitter<Array<any>>();
  pager: any = {};

  constructor(private pagination: NgPaginationService) {
    this.showTotal = false;
   }

  ngOnInit() {
    this.setPage(1)
  }

  setPage(page: number) {
    // get pager object from service
    this.pager = this.pagination.getPager(this.allItems.length, page, this.pageSize, this.maxPages);

    // get current page of items
    this.pagedItems.emit(this.allItems.slice(this.pager.startIndex, this.pager.endIndex + 1)) 
  }

}
